import React from "react";

import "./style.css";

import Arrows from "../arrows";
import Button from "../button";
import ButtonRouter from "../buttonrouter";
import List from "../list";

export default class Controller extends React.Component {
  state = {
    displayOptions: false
  }

  toggleOptions = () => {
    const displayOptions = !this.state.displayOptions;

    this.setState({
      displayOptions
    })
  }

  render() {
    const { restartLevel, undoMove, isDone, memory, id } = this.props.data;
    const { handleButton } = this.props;
    const { displayOptions } = this.state;
    const options = [
      <Button value={<span className="fa fa-refresh"></span>} handleClick={restartLevel}/>,
      <ButtonRouter attribute={{href: '/', value: <span className="fa fa-home"></span>}} bordered/>,
      isDone ? 
        <ButtonRouter attribute={{href: `/game/${id + 1}`, value: <span className="fa fa-level-up"></span>}} bordered/> : 
        <Button value={<React.Fragment><span className="fa fa-history"></span> {memory.length}</React.Fragment>} handleClick={undoMove} />
    ]
    const message = <h2 className="content__title">Good job!</h2>

    return (
      <React.Fragment>
        <div className="controller">
          <Button handleClick={this.toggleOptions} value={<span className="fa fa-bars"></span>}/>
          {displayOptions ? 
            <List items={options}/> :
            isDone ? 
              message: 
              <Arrows handleButton={handleButton}/>     
          }
        </div>
        <div className="controller--desktop">
          <List items={options}/>
          {isDone ?
            message :
            <Arrows handleButton={handleButton}/>  }
        </div>
      </React.Fragment>
    );
  }
}
